}
@media screen and (min-width: 1200px) {
  .catalog__item {
    width: 25%;
  }
}
@media screen and (min-width: 1440px) {
  .catalog__item {
    width: 33.3333333333%;
  }
}
.catalog__item_long {
  width: 100%;
}
@media screen and (min-width: 480px) {
  .catalog__item_long {
    width: 50%;
  }
}
@media screen and (min-width: 600px) {
  .catalog__item_long {
    width: 100%;
  }
}
@media screen and (min-width: 768px) {
  .catalog__item_long {
    width: 100%;
  }
}
@media screen and (min-width: 990px) {
  .catalog__item_long {
    width: 66.6666666667%;
  }
}
@media screen and (min-width: 1200px) {
  .catalog__item_long {
    width: 50%;
  }
}
@media screen and (min-width: 1440px) {
  .catalog__item_long {
    width: 66.6666666667%;
  }
}
.catalog__btns {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 -15px;
  padding-bottom: 10px;
}
.catalog__show-more {
  padding: 0 15px;
  margin-bottom: 20px;
}
.catalog__pagination {
  padding: 0 15px;
  margin-bottom: 20px;
}
.catalog__arrow-up {
  padding: 0 15px;
  margin-bottom: 20px;
}
.catalog__open-filter {
  min-width: 290px;
  display: block;
  background: #fff;
  border: 2px solid #eff0f4;
  padding: 20px;
  font: 400 20px/22px "Fira Sans", sans-serif;
  color: #2c2f2e;
  margin-bottom: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
}
.catalog__open-filter:hover, .catalog__open-filter:active {
  color: #E83F33;
  box-shadow: 1px 2px 13px 0px rgba(18, 46, 72, 0.05);
}
@media screen and (min-width: 480px) {
  .catalog__open-filter {
    min-width: 320px;
  }
}
@media screen and (min-width: 1440px) {
  .catalog__open-filter {
    display: none;
  }
}
.catalog__filter {
  max-width: 320px;
  margin-bottom: 30px;
  display: none;
}
@media screen and (min-width: 1440px) {
  .catalog__filter {
    display: block !important;
  }
}